import empty from '@/components/empty-data.vue'
export default {
	components: {
		empty
	},
	data() {
		return {
			list: []
		};
	},
	onLoad() {},
	onShow() {
		this.api.center.address.get_list({}, res => {
			console.log(res)
			this.list = res.data
		}, err => {

		})
	},
	methods: {

		edit(id) {
			uni.navigateTo({
				url: '../address-handle/address-handle?id=' + id
			})
		},
		deleted(id, index) {
			uni.showModal({
				title: "确定要删除吗？",
				success: (res) => {
					if (res.confirm) {
						this.api.center.address.delete({
							id
						}, res => {
							uni.showToast({
								title: res.message,
								icon: "success",
								success: () => {
									this.list.splice(index, 1)
								}
							})
						}, err => {

						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})

		},
		addressAdd() {
			uni.navigateTo({
				url: "../address-handle/address-handle"
			})
		}
	}
}
